<template>
    <div>
        <h3>Menu3: </h3>
        <input type="password" v-model="state.pw" placeholder=" " />
        <button @click="handleDeleteAction"></button>
    </div>
</template>

<script>
import { reactive } from 'vue';
import axios from 'axios';
import { useRoute } from 'vue-router';
import { useStore } from 'vuex';

export default {
    setup () {
        const store = useStore();

        const router = useRoute();

        const state = reactive({
            pw: '',
            token: sessionStorage.getItem("TOKEN")
        });

        const handleDeleteAction = async() => {
            if (confirm(' ?')) {
                const url = `/member/delete`;
                const headers = {
                    "Content-Type":"application/json",
                    "token":state.token
                };
                const body = {
                    password: state.pw
                };
                const response = await axios.delete(url, {headers:headers, data:body});
                console.log(response.data);

                if (response.data.status === 200) {
                    sessionStorage.removeItem("TOKEN");
                    alert(' .');
                    
                    // URL  
                    router.push({name:'Home'});
                    
                    //   /
                    store.commit("setMenu","/");

                    //   false
                    store.commit("setLogged", false);
                }
            }
        };

        return {state, handleDeleteAction}
    }
}
</script>

