<template>
    <div class="style1">
        <h3>src/components/Join.vue</h3>

        {{state.userrole}}
        <el-form
            label-width="100px"
            :model="state"
            style="max-width: 460px">

            <el-form-item label="">
                <el-input v-model="state.userid" @keyup="handleEmailCheck" ></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="state.userpw"></el-input>
            </el-form-item>

            <el-form-item label=" ">
                <el-input v-model="state.userpw1"></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="state.username"></el-input>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click="handleJoin"></el-button>
            </el-form-item>
        </el-form>

         :
        <select v-model="state.userrole">
            <option value="CUSTOMER"></option>
            <option value="SELLER"></option>
        </select> <br />

    </div>
</template>

<script>
import { reactive, ref } from 'vue'
import axios from 'axios'
import {useRouter} from 'vue-router';

export default {
    setup () {
        const router = useRouter();
        
        // High  :  
        const state = reactive({
            userid: 'aaa',
            userpw: '',
            userpw1: '',
            username: '',
            useremailcheck: '',
            userrole : 'CUSTOMER'
        });

        // Low  :  
        const userid = ref(null); //  bbb  
        const userpw = ref(null);
        const userpw1 = ref(null);
        const username = ref(null);

        //    
        const validEmail = (email) => {
            // 
            var re = /[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]*$/i;
            return re.test(email);
        }

        //  
        const handleEmailCheck = async() => {
            if (validEmail(state.userid)) {
                console.log(state.userid);
                const url = `/member/emailcheck?email=${state.userid}`;
                const headers = {"Content-Type":"application/json"};
                const response = await axios.get(url, {headers});
                console.log(response.data);
                if (response.data.status === 200) {
                    if (response.data.result === 1) {
                    state.useremailcheck = '';
                    }
                    else {
                        state.useremailcheck = '';
                    }
                }
            }
            else{
                state.useremailcheck = '';
            }
        }

        // function handleJoin(){}
        const handleJoin = async() => {
            if(state.userid === ''){
                alert(' .');
                userid.value.focus(); // 

                return false;
            } //   (else if X)
            if(state.userpw === ''){
                alert(' .');
                userpw.value.focus();
                return false;
            }
            if(state.userpw !== state.userpw1){
                alert(' .');
                userpw1.value.focus();
                return false;
            }
            if(state.username === ''){
                alert(' .');
                username.value.focus();
                return false;
            }//      

            if (state.useremailcheck !== '') {
                alert(' ');
            }

            //      
            const url = "/member/insert";
            const headers = {"Content-Type":"application/json"};
            const body = {
                email: state.userid,
                password: state.userpw,
                name: state.username,
                role: state.userrole
            };
            const response = await axios.post(url, body, {headers});
            console.log(response.data);
            if (response.data.status === 200) {
                alert(' .');
                router.push({name:'Home'});
            }
        }

        return {
            state,
            userid,
            userpw,
            userpw1,
            username,
            handleJoin,
            handleEmailCheck
        }
    }
}
</script>

<!-- scss, less => css -->
<!-- npm install -D sass-loader@^10 sass -->
<style lang="scss" scoped>
    .style1 {
        border: 1px solid #cccccc;
        padding: 20px;
    }
</style>