<template>
    <div>
        <h3>Login.vue</h3>
        <el-form :inline="true" :model="state" class="demo-form-inline">
            <el-form-item label="">
                <el-input placeholder=" " v-model="state.userid"></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input placeholder=" " v-model="state.userpw"></el-input>
            </el-form-item>
            
            <el-form-item>
                <el-button type="primary" @click="handleLogin"></el-button>
            </el-form-item>
        </el-form>
        
    </div>
</template>

<script>
import { reactive } from 'vue';
import axios from 'axios';
import {useRouter} from 'vue-router';
import {useStore} from 'vuex';

export default {
    setup () { // this  
        const router = useRouter();
        const store = useStore();

        const state = reactive({
            userid: 'asd@b.com',
            userpw: '1234'
        });

        const handleLogin = async() => {
            const url = `/member/select`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                email: state.userid,
                password: state.userpw
            };
            const response = await axios.post(url, body, {headers});
            console.log('response.data---->', response.data);
            if (response.data.status === 200) {
                //   ()
                sessionStorage.setItem("TOKEN", response.data.token);
                alert(' .');

                // actios   store/state  
                store.dispatch("handleData", {});
                // // store/index.js state  
                // // , , ,  ()
                // store.commit("setUid", response.data.uid);
                // store.commit("setUname",response.data.uname);
                // store.commit("setUrole",response.data.urole);
                // store.commit("setLogged", true); // 3.  


                const curl = sessionStorage.getItem("CURL");
                if (curl === null) {
                    // 1.  
                    router.push({name:'Home'});

                    // App.vue    
                    // 2.  
                    store.commit("setMenu","/");
                }
                else {  //    
                    const query = JSON.parse(sessionStorage.getItem("CURL_QUERY"));
                    const params = JSON.parse(sessionStorage.getItem("CURL_PARAMS"));

                    router.push({name:curl, query:query, params:params});
                }

                
            }
        };

        return {state, handleLogin}
    }
}
</script>

