<template>
    <div>
        <h3>Menu2: </h3>
        <input type="password" v-model="state.pw" placeholder=" "/>
        <input type="password" v-model="state.pw1" placeholder=" "/>
        <input type="password" v-model="state.pw2" placeholder="  "/>
        <button @click="handleUpdateAction"></button>
    </div>
</template>

<script>
import { reactive } from 'vue';
import axios from 'axios';
import {useRouter} from 'vue-router';
import { useStore } from 'vuex';

export default {
    setup () {
        const store = useStore();

        const router = useRouter();

        const state = reactive({
            pw:'',
            pw1:'',
            pw2:'',
            token: sessionStorage.getItem("TOKEN")
        });

        const handleUpdateAction = async() => {
            if (state.token !== null) {
                const url = `/member/updatepw`;
                const headers = {
                    "Content-Type":"application/json",
                    "token": state.token
                };
                const body = {
                    password: state.pw,
                    password1: state.pw1
                };
                const response = await axios.put(url, body, {headers});
                console.log(response.data);
                if (response.data.status === 200) {
                    alert('  ');
                }
            }
            else{
                // URL  
                router.push({name:'Home'});

                //   /
                store.commit("setMenu","/");

                //   false
                store.commit("setLogged", false);
            }
        }
        

        return {state, handleUpdateAction}
    }
}
</script>

