<template>
    <div>
        <h3></h3>

        <h5> </h5>
        <input type="text" v-model="state.address" placeholder=""/> <br />
        <button @click="handleInsertaddr"></button> <br />
        <hr />

        <h5> </h5>
        <div v-if="state.items">

            <table border="1">
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                <tr v-for="(item, idx) in state.items" :key="item">
                    <td>{{item.chk}}</td>
                    <td><input type="text" v-model="item.address" /></td>
                    <td>
                        <button @click="handleAddressUpdate(idx)"></button>
                        <button @click="handleAddressDelete(item._id)"></button>
                        <button @click="handleChkUpdate(item._id)"></button>
                    </td>
                </tr>
            </table>

        </div>
        

        
    </div>
</template>

<script>
import axios from 'axios';
import { onMounted, reactive } from 'vue';

export default {
    setup () {
        const state = reactive({
            address: '',
            chk:0,
            token: sessionStorage.getItem("TOKEN"),
        });

        const handleLoadData = async()=> {
            const url = `/member/selectaddr`;
            const headers = {
                "Content-Type":"application/json",
                "token": state.token
            };
            const response = await axios.get(url, {headers});
            console.log(' ------>', response.data);
            if (response.data.status === 200) {
                state.items = response.data.result;
            }
        }

        onMounted( ()=> {
            handleLoadData();
        });

        //  
        const handleInsertaddr = async()=> {
            const url = `/member/insertaddr`;
            const headers = {
                "Content-Type":"application/json",
                "token": state.token
            };
            const body = {
                address: state.address
            };
            const response = await axios.post(url, body, {headers});
            console.log('------>',response);
            if (response.data.status === 200) {
                alert('.');
            }
        }

        //  
        const handleAddressDelete = async(no)=> {
            if (confirm('?')) {
                const url = `/member/deleteaddr`;
                const headers = {
                    "Content-Type":"application/json",
                    "token": state.token
                };
                const body = {no : no};

                const response = await axios.delete(url, {headers:headers, data:body});

                console.log(' ------>', response.data);
                if (response.data.status === 200) {
                    alert('.')
                    handleLoadData();
                }
            }
        }

        //  
        const handleChkUpdate = async(no) => {
            if (confirm('?')) {
                const url = `/member/updatechkaddr`;
                const headers = {
                    "Content-Type":"application/json",
                    "token": state.token
                };
                const body = {no : no};

                const response = await axios.put(url, body, {headers:headers});

                console.log(' ------>', response.data);
                if (response.data.status === 200) {
                    alert('.')
                    handleLoadData();
                }
            }
        }

        //  
        const handleAddressUpdate = async(idx)=> {
            if (confirm('?')) {
                const url = `/member/updateaddr`;
                const headers = {
                    "Content-Type":"application/json",
                    "token": state.token
                };

                const body = {
                    no : state.items[idx]._id,
                    address: state.items[idx].address
                };

                const response = await axios.put(url, body, {headers:headers});
                console.log(' ------>', response.data);

                if (response.data.status === 200) {
                    alert('.');
                    handleLoadData();
                }
            }
            
        }

        return {
            state, 
            handleInsertaddr,
            handleAddressDelete,
            handleChkUpdate,
            handleAddressUpdate
            
        }
    }
}
</script>

