// : src/store/index.js
import { createStore } from "vuex";
import axios from 'axios';


//      
// props  emit  .
const stores = createStore({

    //   
    state:{
        menu: sessionStorage.getItem("CURL"),   //  
        logged: false,  //  
        uid : '',   //    
        uname: '',  //   
        token: '',  //     
        urole: '',   //    
    },

    //  (getter)
    getters: {
        getUrole(state){
            console.log('getUrole------>',state.urole);
            return state.urole;
        },
        getUid(state){
            return state.uid;
        },
        getUname(state){
            return state.uname;
        },
        getMenu(state){
            return state.menu;
        },
        getLogged(state){
            return state.logged;
        }
    },

    //  (mutation) :  
    mutations:{
        setUrole(state, val){
            console.log('setUrole---->', val);
            state.urole = val;
        },
        setUid(state, val){
            state.uid = val;
        },
        setUname(state, val){
            state.uname = val;
        },
        setMenu(state, value){
            state.menu = value;
        },
        setLogged(state, value){
            state.logged = value;
        }
    },

    //  (action)
    //   ,    :     
    actions:{
        async handleData(context, payload){
            console.log(payload);
            const token = sessionStorage.getItem("TOKEN");
            if (typeof token !== 'undefined' && token !== null) {
                const url = `/member/validation`;
                const headers = {
                    "Content-Type":"application/json",
                    "token" : token
                };
                const response = await axios.get(url, {headers:headers});
                if (response.data.status === 200) {
                    // mutations setUid, setUname   
                    context.commit("setUid", response.data.uid);
                    context.commit("setUname", response.data.uname);
                    context.commit("setUrole", response.data.urole);
                    context.commit("setLogged", true);
                }
                else{
                    //      
                    context.commit("setLogged", false);
                }
            }
        }
    }

});

export default stores;