<template>
    <div>
        <h3>BoardWrite.vue</h3>
        {{state}},
        <hr/>
        : <input type="text" v-model="state.title" /> <br />

        : 
            <div style="width:600px">
                <ckeditor 
                :editor="editor.editor"
                :config="editor.editorConfig"
                v-model="state.content"></ckeditor>
            </div>
            
        : <input type="text" v-model="state.writer" /><br />

        

        : 
            <img :src="state.imgurl" style="width:100px;height:100px" />
            <input type="file" @change="handleImage" /><br />

        <button @click="handleWrite"></button><br />
    </div>
</template>

<script>
import {reactive} from 'vue';

import ClassicEditor from '@ckeditor/ckeditor5-build-classic';

import {useRouter} from 'vue-router';

import axios from 'axios';

export default {

    setup () {

        const router = useRouter();

        //object
        const editor = {
            editor: ClassicEditor,
            editorData: '<p></p>',
            editorConfig: {}
        }

        const state = reactive({
            title   : '',
            content : '',
            writer   : '',
            imgdata : '',
            imgurl  : require('../assets/default.jpg'),
        });


        //  or 
        const handleImage = (e) => {
            console.log(e); //   
            if(e.target.files[0]){ //
                state.imgdata = e.target.files[0];
                //       URL
                state.imgurl = URL.createObjectURL(e.target.files[0]);
            }
            else{ //
                state.imgdata = '';
                state.imgurl = require('../assets/default.jpg');
            }
        };

        const handleWrite = async() => {
            if(state.title === ''){
                alert(' .');
                return false;
            }
            if(state.content === ''){
                alert(' .');
                return false;
            }
            if(state.witer === ''){
                alert(' .');
                return false;
            }
            if(state.imgdata === ''){
                alert(' .');
                return false;
            }

            //     
            const url = `/board/insert`;
            const headers = {"Content-Type":"multipart/form-data"};
            const body = new FormData();    //   
            body.append("title", state.title);
            body.append("content", state.content);
            body.append("writer", state.writer);
            body.append("image", state.imgdata);

            const response = await axios.post(url, body, {headers});
            console.log(response.data);

            if(response.data.status === 200){
                alert(' ');
                router.push({name: "Board"});
            }
        };

        return {
            state, 
            handleImage, 
            handleWrite, 
            editor,
        }
    }
}
</script>

