<template>
    <div>
        <h3>  </h3>
        
        <button @click="handleAdd"> </button>
        <button @click="handleSub"> </button>
        {{state.items}}
        <table border="1">
            <tr v-for="(tmp, idx) in state.cnt" :key="tmp">
                <td><input type="file" @change="handleImage($event, idx)" /></td>
                <td><input type="text" placeholder="" v-model="state.items[idx].name"/></td>
                <td><input type="text" placeholder="" v-model="state.items[idx].price"/></td>
                <td><input type="text" placeholder="" v-model="state.items[idx].quantity"/></td>
                <td><input type="text" placeholder="" v-model="state.items[idx].content"/></td>
            </tr>
        </table>
        <button @click="handleInsertAction"></button>
    </div>
</template>

<script>
import { reactive } from 'vue';
import axios from 'axios';
import { useRouter } from 'vue-router';

export default {
    setup () {
        const router = useRouter();

        const state = reactive({
            cnt: 2,
            token: sessionStorage.getItem("TOKEN"),
            items: [
                {
                    image:'',
                    name:'',
                    price:'541564',
                    quantity:'564',
                    content:'',
                },
                {
                    image:'',
                    name:'',
                    price:'541564',
                    quantity:'564',
                    content:'',
                }
            ] 
        });

        const handleAdd = () => {
            state.cnt++;
            // state.items  {} 
            state.items.push({
                image:'',
                name:'',
                price:'541564',
                quantity:'564',
                content:'',
            })
        };

        const handleSub = () => {
            if (state.cnt >= 3) { // 1   2
                state.cnt--;    //   
                // state.items  {} 
                state.items.pop();
            }
        };

        //    
        const handleImage = (e, idx)=> {
            console.log(e); //   
            console.log(idx);   // 
            if (e.target.files[0]) {
                
                state.items[idx].image = e.target.files[0];
            }
            else{
                state.items[idx].image = '';
            }
            
        }

        const handleInsertAction = async()=> {
            const url = `/seller/insert`;
            const headers = {
                "Content-Type":"multipart/form-data",
                "token":state.token
            };
            const body = new FormData();
            // state.item = [{}, {}]
            for(let i=0;i<state.items.length;i++){
                body.append("image", state.items[i].image);
                body.append("title", state.items[i].name);
                body.append("price", state.items[i].price);
                body.append("quantity", state.items[i].quantity);
                body.append("content", state.items[i].content);
            }

            const response = await axios.post(url, body, {headers});
            console.log(response.data);
            if (response.data.status === 200) {
                alert('.');
                router.push({name:"Seller"});
            }
        }

        return {state,handleAdd, handleSub, handleInsertAction, handleImage}
    }
}
</script>

