<template>
    <div>
        <h3>BoardContent.vue</h3>

        <div v-if="state.item">
            : {{state.item.title}} <br />
            : {{state.item.content}} <br />
            : {{state.item.writer}} <br />
            : <img :src="state.item.imageurl" style="width:200px" /> <br />
            <hr />

            <button @click="handleDelete"></button>
            <button @click="handleUpdate"></button>
            <button v-if="state.item.prev > 0" @click="handleData(1)"></button>
            <button v-if="state.item.next > 0" @click="handleData(2)"></button>
            <hr />

            <div v-for="tmp in state.reply" :key="tmp">
                {{tmp._id}}
                {{tmp.content}}
                {{tmp.writer}}
                {{tmp.regdate}}
                <button @click="handleReplyDelete(tmp._id)"> </button>
            </div>
            {{state.reply1}} <br />
            <textarea rows="6" placeholder="" v-model="state.reply1.content"></textarea> <br />
            <input type="text" placeholder="" v-model="state.reply1.writer"/> <br />
            <button @click="handleInsertReply"> </button>

        </div>
        
    </div>
</template>

<script>

import axios from 'axios';  // 
import { useRoute } from 'vue-router';  //    route.query
import { useRouter } from 'vue-router';  //  
import { reactive, onMounted } from 'vue';   //state


export default {
    setup () {
        const route = useRoute();   //  
        const router = useRouter(); //

        // state  
        const state = reactive({
            no  : route.query.no,
            reply: {},
            reply1 : {
                content : '',
                writer  : ''
            }
        });

        const handleReplyDelete = async(no) => {
            const url = `/board/deletereply?no=${no}`;
            const headers = {"Content-Type":"application/json"};
            const response = await axios.delete(url, {headers:headers, data:{}});

            console.log(response.data);
            if (response.data.status === 200) {
                await handleReplyMount(state.no);
            }
        };

        const handleInsertReply = async() => {
            const url = `/board/insertreply`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                boardno: state.no,
                content: state.reply1.content,
                writer: state.reply1.writer,
            }
            const response = await axios.post(url, body, {headers});
            console.log(response.data);

            if (response.data.status === 200) {
                alert('  ');
                await handleReplyMount(state.no);
            }

        };


        //     
        const handleUpdate = () => {
            router.push({name:"BoardUpdate", query:{no: state.no}});
        }

        const handleDelete = async() => {
            if (confirm('?')) {
                const url = `/board/delete?no=${state.no}`;
                const headers = {"Content-Type":"application/json"};
                const response = await axios.delete(url, {headers:headers, data:{}});

                console.log(response.data);
                if (response.data.status === 200) {
                    router.push({name:"Board", query:{page:1, text:''}});
                }
            }
        };

        const handleMount = async(no) => {

            const url = `/board/selectone?no=${no}`
            const headers = {"Content-Type":"application/json"};

            const response = await axios.get(url, {headers});
            console.log(response.data);
            if (response.data.status === 200) {
                console.log(response.data.result);
                state.item = response.data.result;
            }
        };

        //
        const handleReplyMount = async(no) => {

            const url = `/board/selectreply?no=${no}`;
            const headers = {"Content-Type":"application/json"};

            const response = await axios.get(url, {headers});
            console.log(response.data);
            if (response.data.status === 200) {
                state.reply = response.data.result;
            }
            
        }

        onMounted( async() => {
            await handleMount(state.no);
            await handleReplyMount(state.no);
        });

        // ,  
        const handleData = async(idx) => {
            if (idx === 1) {    // 
                //  
                //   ->   onMounted()
                router.push({name:"BoardContent", query:{no: state.item.prev}});
                state.no = state.item.prev;
                await handleMount(state.no);
                await handleReplyMount(state.no);
            }
            else if (idx === 2) {   // 
                router.push({name:"BoardContent", query:{no: state.item.next}});
                state.no = state.item.next;
                await handleMount(state.no);
                await handleReplyMount(state.no);
            }
        };

        return {
            state,
            handleDelete,
            handleData,
            handleUpdate,
            handleInsertReply,
            handleReplyDelete
        }
    },

}

</script>

<style lang="scss" scoped>

</style>